<template>
  <div class="task-wrap">
    <div class="no-data" v-if="taskList.length <= 0">
      {{ $t('admin.noTask') }}
    </div>
    <template v-else>
      <TaskItem
        :ref="'item' + item.id"
        v-for="(item, index) in taskList"
        :key="item.id"
        :index="index"
        :dataSource="item"
        :type="1"
        @show="handleShowUpload"
        @reject="handleReject"
        @close="
          () => {
            uploadShow = false
          }
        "
      ></TaskItem>
    </template>
    <TaskPop
      :taskId="tempId"
      v-if="uploadShow"
      @success="handleUploadSuccess"
    ></TaskPop>
    <CommomPop
      v-if="rejectShow"
      @close="
        () => {
          rejectShow = false
        }
      "
    >
      <div class="title">{{ $t('admin.rejectReason1') }}</div>
      <div class="reason">{{ taskList[tempIndex].reason }}</div>
    </CommomPop>
  </div>
</template>

<script>
import { handleMyList } from '@/services/task'
import TaskItem from './components/TaskItem.vue'
import TaskPop from './components/TaskPop.vue'
import CommomPop from '../../components/common/CommomPop.vue'
export default {
  name: 'Task',
  data () {
    return {
      taskList: [],
      uploadShow: false,
      tempId: '',
      tempIndex: null,
      rejectShow: false
    }
  },
  methods: {
    async getList () {
      const resp = await handleMyList({
        orderByColumn: 'log.id',
        isAsc: 'desc'
      })
      if (!resp.success) return
      this.taskList = resp.rows
    },
    handleShowUpload (temp) {
      this.tempId = temp.id
      this.uploadShow = true
      console.log('this.uploadShow', this.uploadShow)
    },
    async handleReject (index) {
      this.tempIndex = index
      this.rejectShow = true
    },
    handleUploadSuccess () {
      this.$refs[`item${this.tempId}`][0].taskInfo.submitQuantity += 1
      this.uploadShow = false
    }
  },
  mounted () {
    this.getList()
  },
  components: { TaskItem, TaskPop, CommomPop }
}
</script>

<style scoped lang="scss">
.task-wrap {
  padding: 15px;
  color: #fff;
}
.reason {
  margin-top: 30px;
  margin-bottom: 20px;
}
</style>
